import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import Loading from './Loading';

declare global {
  interface Window {
    kakao: any;
  }
}

const { kakao } = window;

const sampleData = [
  {
    title: 1,
    coords: new kakao.maps.LatLng(37.499242, 127.0256595),
    alt: '   ',
    content: '   !   ~',
    type: 'soil'
  },
  {
    title: 2,
    coords: new kakao.maps.LatLng(37.5072425, 127.012159),
    alt: '        ',
    content: '      :)',
    type: 'water'
  },
  {
    title: 3,
    coords: new kakao.maps.LatLng(37.5032422, 127.021158),
    alt: '    ',
    content: '  ?',
    type: 'seed'
  },
  {
    title: 4,
    coords: new kakao.maps.LatLng(37.499241, 127.0215592),
    alt: '   ',
    content:
      '    !     :)',
    type: 'sun'
  }
];

const KakaoMap = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [location, setLocation] = useState({ latitude: 0, longitude: 0 });
  const navigate = useNavigate();

  //  (, ) .
  const getPosSuccess = async (pos: GeolocationPosition) => {
    setLocation({
      latitude: pos.coords.latitude,
      longitude: pos.coords.longitude
    });
    setIsLoading(false);

    const root = document.getElementById('map');
    if (root) root.style.display = 'block';
  };

  const getCurrentPosition = async () => {
    navigator.geolocation.getCurrentPosition(
      getPosSuccess,
      () => alert('   .'),
      {
        enableHighAccuracy: false,
        maximumAge: 30000,
        timeout: 27000
      }
    );
  };

  useEffect(() => {
    getCurrentPosition();
  }, []);

  useEffect(() => {
    window.kakao.maps.load(() => {
      const container = document.getElementById('map');
      const options = {
        center: new window.kakao.maps.LatLng(
          location.latitude,
          location.longitude
        ),
        level: 6
      };

      const map = new window.kakao.maps.Map(container, options);
      const imageSize = new kakao.maps.Size(34, 45);

      sampleData.forEach((data) => {
        const markerImageSource = `https://toyou-bucket.s3.ap-northeast-2.amazonaws.com/${data.type}.svg`;
        const markerImage = new kakao.maps.MarkerImage(
          markerImageSource,
          imageSize
        );
        const marker = new kakao.maps.Marker({
          map: map,
          position: data.coords,
          title: data.title,
          image: markerImage,
          clickable: true
        });
        marker.setMap(map);
        kakao.maps.event.addListener(marker, 'click', () => {
          navigate('/mission', {
            state: { id: marker.Gb }
          });
        });
      });

      const markerImage = new kakao.maps.MarkerImage(
        'https://toyou-bucket.s3.ap-northeast-2.amazonaws.com/current.svg',
        imageSize
      );

      const currentMarker = new kakao.maps.Marker({
        position: options.center,
        image: markerImage,
        opacity: 1
      });
      currentMarker.setMap(map);

      const drawingCircle = new kakao.maps.Circle({
        strokeWeight: 2, //  
        strokeColor: '#36A400', //  
        strokeOpacity: 1,
        strokeStyle: 'dashed',
        fillColor: '#36A400', //  
        fillOpacity: 0.2,
        radius: 1000 // 1km
      });
      const circleOptions = {
        center: options.center,
        radius: length
      };

      //     
      drawingCircle.setOptions(circleOptions);
      drawingCircle.setMap(map);
    });
  }, [location]);

  return (
    <>
      {isLoading && <Loading />}
      <MapContainer id='map' style={{ display: 'none' }}></MapContainer>
    </>
  );
};

const MapContainer = styled.div`
  margin: 1.5rem auto 0 auto;
  width: 90%;
  height: 60vh;
`;

export default KakaoMap;
